$--header-height: 48px;

.layout {
  position: relative;
  min-height: 100%;

  &__content {
    &.fixed {
      width: 100%;
      max-width: 1200px;
      margin: 0 auto;
    }

    &.follow {
      width: 100%;
    }
  }
}

.sider {
  position: relative;
  z-index: 2;
  min-height: 100%;
  overflow: hidden;
  box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
  transition: none;

  ::-webkit-scrollbar {
    width: 6px;
    height: 6px;
  }

  ::-webkit-scrollbar-track {
    background: hsla(0, 0%, 100%, 0.1);
    border-radius: 3px;
    -webkit-box-shadow: inset 0 0 5px rgba(37, 37, 37, 0.05);
  }

  ::-webkit-scrollbar-thumb {
    background: hsla(0, 0%, 100%, 0.15);
    border-radius: 3px;
    -webkit-box-shadow: inset 0 0 5px hsla(0, 0%, 100%, 0.05);

    &:hover {
      background-color: hsla(0, 0%, 100%, 0.2);
    }
  }

  :global(.ant-layout-sider-children) {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  &:global(.ant-layout-sider-light) {
    box-shadow: 2px 0 8px 0 rgba(29, 35, 41, 0.05);

    ::-webkit-scrollbar-track {
      background: rgba(0, 0, 0, 0.06);
      border-radius: 3px;
      -webkit-box-shadow: inset 0 0 5px rgba(0, 21, 41, 0.05);
    }

    ::-webkit-scrollbar-thumb {
      background: rgba(0, 0, 0, 0.12);
      border-radius: 3px;
      -webkit-box-shadow: inset 0 0 5px rgba(0, 21, 41, 0.05);

      &:hover {
        background-color: rgba(0, 0, 0, 0.18);
      }
    }

    .layout__sider-header {
      color: #1890ff;
    }
  }
}

.menu {
  flex: 1 1 0;
  padding: 16px 0;
  overflow: hidden auto;
  border-right-color: transparent;

  &.no-padding {
    padding: 0;
  }

  &:global(.ant-menu-inline-collapsed) {
    width: 100%;

    :global(.ant-menu-item),
    :global(.ant-menu-submenu-title) {
      padding: 0 16px;
    }
  }
}

.header {
  height: $--header-height;
  padding: 0;
  line-height: $--header-height;
  background-color: #fff;

  &__inner {
    display: flex;
    align-items: center;
    width: 100%;
    height: inherit;
    padding-right: 16px;
    background-color: inherit;
    box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
    transition: background 0.3s, width 0.2s;

    &.fixed {
      position: fixed;
      top: 0;
      left: 0;
    }

    &.z-index-3 {
      z-index: 3;
    }
  }

  &__center {
    flex: auto;
    height: $--header-height;
    overflow: hidden;
  }

  &__right {
    display: flex;
    float: right;
    height: $--header-height;
    overflow: hidden;
  }

  &__action {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 12px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;

    &:hover {
      background-color: rgba(0, 0, 0, 0.025);
    }
  }

  &__avatar {
    color: #f56a00;
    background-color: #fde3cf;
  }

  &__username {
    margin-left: 8px;
    font-size: 14px;
  }

  &__menu-fold {
    height: $--header-height;
    padding: 0 24px;
    font-size: 16px;
    cursor: pointer;
  }

  .menu {
    height: $--header-height;
    padding: 0;
    line-height: $--header-height - 2;
    border: none;
  }

  &.dark {
    color: hsla(0, 0%, 100%, 0.85);
    background-color: #001529;

    .header__inner {
      box-shadow: 0 1px 4px rgba(0, 21, 41, 0.12);
    }

    .header__action:hover {
      background-color: #1890ff;
    }
  }

  &.light {
    background-color: #fff;
  }
}

.site {
  display: flex;
  align-items: center;
  justify-content: center;
  height: $--header-height;
  overflow: hidden;
  font-size: 18px;
  font-weight: 400;
  color: #fff;
  cursor: pointer;

  &__icon {
    height: 32px;
    vertical-align: middle;
  }

  &__title {
    margin-left: 12px;
  }

  &.dark {
    color: #fff;
  }

  &.light {
    color: #1890ff;
  }
}
